  }>>(props?.contentText);
  const defaultMessageContent = ref<string>(props?.messageContent?.custom || "[自定义消息]");
  return {
    TUITranslateService,
    CHAT_MSG_CUSTOM_TYPE,
    isUrl,
    star,
    extensionJSON,
    businessID,
    descriptionForShow,
    defaultMessageContent
  };
};
__sfc_main.components = Object.assign({
  Icon
}, __sfc_main.components);
export default __sfc_main;
